<template>
  <div class="home">
    <div class="swiper-container">
      <!-- 需要进行轮播的部分 -->
      <div class="swiper-wrapper">
        <!-- 每个节点 -->
        <div class="swiper-slide" v-for="(swiper,index) in swipers" :key="index">
          <img :src="swiper.src" :alt="swiper.alt" />
        </div>
      </div>
      <!--如果需要使用分页器-->
      <div class="swiper-pagination swiper-pagination-white"></div>

      <!-- 如果需要使用前进后退按钮 -->
      <div class="swiper-button-prev swiper-button-white"></div>
      <div class="swiper-button-next swiper-button-white"></div>
    </div>
    <div class="cards">
      <div  v-for="(card,index) in cards" :key="index" @click="cardClickHandler(card.loaction)">
        <Card style="width:440px" >
          <div>
            <img :src="card.src">
            <h3>{{card.title}}</h3>
          </div>
        </Card>
      </div>
    </div>
  </div>
</template>

<script>
// 引入组件iview
import Card from 'iview/src/components/card'
// 引入轮播插件
import Swiper from 'swiper'
import cssUrl from '../../assets/css.png'
import jsUrl from '../../assets/js.png'
import nodejsUrl from '../../assets/nodejs.png'
import vueUrl from '../../assets/vuepng.png'
import reactUrl from '../../assets/react.gif'
import javaUrl from '../../assets/java1.png'
import socket from '../../assets/socket.gif'
import todo from '../../assets/todo.gif'
import yun from '../../assets/yun.png'
import plane from '../../assets/plane.png'
export default {
  name: 'home',
  components: {
    Card,
  },
  data() {
    return {
      swipers: [
        {
          src: cssUrl,
          alt: 'css图片',
        },
        {
          src: jsUrl,
          alt: 'js图片',
        },
        {
          src: nodejsUrl,
          alt: 'nodejs图片',
        },
        {
          src: vueUrl,
          alt: 'vue图片',
        },
        {
          src: reactUrl,
          alt: 'react图片',
        },
        {
          src: javaUrl,
          alt: 'java图片',
        },
      ],
      cards: [
        {
          src: socket,
          title: '该项目属于Nodejs，采用express框架，运用Socket.IO实现实时聊天通讯',
          loaction: 'https://blog.csdn.net/weixin_64925940/article/details/124663088?spm=1001.2014.3001.5501',
        },
        {
          src: todo,
          title: '该项目属于vue2，通过vue cli创建，vuex进行项目管理，采用模块化理念编写',
          loaction: 'https://blog.csdn.net/weixin_64925940/article/details/124803348?spm=1001.2014.3001.5501',
        },
        {
          src: yun,
          title: '该项目集合前后端一起，前端主要采用jQuery写页面，ajax向后端拿数据，后端采用express连接mysql数据库',
          loaction: 'https://blog.csdn.net/weixin_64925940/article/details/124508428?spm=1001.2014.3001.5501',
        },
        {
          src: plane,
          title: '该项目属于原生JS，通过拿到鼠标坐标移动飞机，通过坐标判断来实现结束，杀敌，得分',
          loaction: 'https://blog.csdn.net/weixin_64925940/article/details/123990761?spm=1001.2014.3001.5501',
        },
      ],
    }
  },
  mounted() {
    this.initSwiper()
  },
  methods: {
    initSwiper() {
      // eslint-disable-next-line no-new
      new Swiper('.swiper-container', {
        loop: true, // 设置轮播的循环方式
        // 设置自动播放间隔时间
        autoplay: 3000,

        // 轮播效果,默认为平滑轮播
        effect: 'slide',

        // 分页器
        pagination: '.swiper-pagination',

        // 前进后退按钮
        prevButton: '.swiper-button-prev',
        nextButton: '.swiper-button-next',

        // 用户中断轮播后续播
        autoplayDisableOnInteraction: false,
      })
    },
    cardClickHandler(location) {
      window.open(location, '_blank')
    },
  },
}
</script>

<style lang="less" scoped>
@import '../../../node_modules/swiper/dist/css/swiper.css';
.swiper-container {
  width: 100%;
  height: auto;
}

.swiper-slide img {
  width: 100%;
  height: 655px;
  object-fit: cover;
}
.cards{
  width: 900px;
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
  margin: 0 auto;
  cursor: pointer;
  & img{
      width: 100%;
      height: 570px
   }
}
.ivu-card{
  margin-top: 20px;

}
</style>
