import Home from '@/views/home/home.vue'
import TopNav from '@/views/topNav/topNav'
import ExploreMore from '@/views/exploreMore/exploreMore'
// 创建 VueRouter 对象

const routes = [ // 静态路由的配置信息
  {
    path: '/',
    redirect: 'home',
  },
  {
    path: '/home', // 当 URL 中访问地址为 /home 时
    name: 'home',
    components: {
      header: TopNav,
      main: Home,
    }, // 拿 Home 组件渲染
  },
  {
    path: '/exploreMore', // 当 URL 中访问地址为 /home 时
    name: 'exploreMore',
    components: {
      header: TopNav,
      main: ExploreMore,
    }, // 拿 Home 组件渲染
  },
]
export default routes
